@import "styles/variables";
@import "styles/mixins";

.wrapper {
  display: flex;
  flex-direction: column;
  padding: 4rem 2rem;
  min-height: 40rem;
  background-color: $colorLightest;
  border-radius: 5rem;
}

.heading {
  text-align: center;
}

.list {
  margin: 2rem 0 0 0;
}

.price {
  margin-top: auto;
  text-align: center;
}

.label {
  @include nav($colorDarkest, 700);
}

.value {
  @include descriptionPromo($colorHighlight, 700);

  margin-left: 1rem;
}
