@import "styles/variables";
@import "styles/mixins";

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3rem 8rem 12rem 8rem;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 70%;
    background-color: $colorMedium;
    border-radius: 3rem;
  }

  & > * {
    position: relative;
    z-index: 1;
  }
}

.checked {
  margin-top: 3rem;
}

.button {
  @include descriptionPromo($colorLightest, 400);

  margin-top: 4rem;
  width: 20rem;
  height: 6rem;
}
