@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  padding-top: 12.5rem;
}

.heading,
.wyziwyg {
  position: relative;
}

.wyziwyg {
  p {
    @include regular($colorDarkest);

    margin: 2rem 0 0 0;
  }

  ul {
    @include resetList;
  }

  li {
    @include listItem;
  }
}
