@import "styles/mixins";

.heading {
  @include visuallyHidden;
}

.wrapper {
  position: relative;
  padding-top: 12rem;
  padding-bottom: 12rem;
}
.iconRight {
  position: absolute;
  bottom: -11rem;
  right: 0;
}

.iconLeft {
  position: absolute;
  bottom: -6rem;
  left: 0;
}

.button {
  display: block;
  margin: 7rem auto 0 auto;
}
