@import "mixins";
@import "fonts";
@import "variables";
@import "functions";

html {
  font-size: 10px;
  overflow: auto;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  outline: none;
}

a {
  text-decoration: none;
}

.visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}

.ReactCollapse--collapse {
  transition: height 500ms;
}

:where(p),
:where(figure) {
  margin: 0;
}

:where(ul) {
  @include resetList;
}

.iconGlasses,
.iconAdaptive {
  position: absolute;
  top: 0;
}

.iconGlasses {
  top: 1.5rem;
  left: 6.5rem;
}

.iconAdaptive {
  left: 7rem;
}
