@import "styles/variables";

.wrapper {
  position: relative;
  padding-top: 3.7rem;
  padding-left: 3.5rem;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 7rem;
    height: 7rem;
    background-color: $colorHighlight;
    border-radius: 50%;
  }

  & > * {
    position: relative;
  }
}

.icon {
  position: absolute;
  top: 4rem;
  right: -6.2rem;
  fill: $colorHighlight;
}

.description {
  color: $colorDarkest;

  margin-top: 2rem;
  opacity: 0.6;
}
