@import "styles/variables";
@import "styles/mixins";

.section {
  position: relative;
}

.wrapper {
  position: relative;
  padding-top: 12rem;
  padding-bottom: 12rem;
}

.circles {
  position: absolute;
  right: -2rem;
  bottom: -6rem;
  width: 26.7rem;
}

.heading {
  color: $colorDarkest;
  text-align: center;
}

.list {
  @include resetList;

  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 25rem;
  margin-top: 5rem;
  padding-right: 9rem;
  padding-left: 13rem;
}
