@import "styles/variables";

.wrapper {
  position: relative;
  --section-radius: 7rem;
  background-color: $colorHighlight;
  border-top-right-radius: var(--section-radius);
  border-top-left-radius: var(--section-radius);
  margin-bottom: -7rem;
  padding-bottom: 7rem;
}

.wrapperLight {
  background-color: $colorLightest;
}
