@import "styles/variables";
@import "styles/mixins";

:where(.wrapper) {
  position: relative;
  width: 36.4rem;
  height: 28rem;
}

.image {
  position: relative;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 1;
}

.icon {
  position: absolute;
  top: -4.7rem;
  right: -8.8rem;
  width: 15.3rem;
  fill: $colorMedium;
}

.label {
  @include regular($colorHighlight);

  position: absolute;
  top: 1.1rem;
  right: -10rem;
  z-index: 2;
}
