@import "styles/variables";
@import "styles/mixins";

.section {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 23.5rem;
    left: calc(50% - 5rem);
    width: 100vw;
    height: 48.5rem;
    background-color: $colorMediumDark;
    border-top-left-radius: 1.5rem;
    border-bottom-left-radius: 30rem;
    opacity: 0.4;
  }
}

.circles {
  position: absolute;
  top: 0;
  left: calc(50%);
  width: 68.2rem;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 65rem 1fr;
  padding-bottom: 19rem;
}

.labelNext {
  @include nav($colorDarkest, 500, true);

  position: absolute;
  top: calc(64.2rem + 7rem);
  left: 7rem;
  display: flex;
  flex-direction: column;
  //padding-left: 12.6rem;
  //padding-left: 12.6rem;
  letter-spacing: 4px;
  transform: rotate(-90deg);
  transform-origin: 0 50%;

  span {
    padding-left: 12.6rem;
    text-transform: lowercase;
  }
}

.arrow {
  //position: absolute;
  //top: -29rem;
  //left: 31rem;
  width: 64.2rem;
  fill: $colorHighlight;
  //transform: rotate(-270deg);
}

.textBlock {
  position: relative;
  padding-top: 32rem;
}

.description {
  @include descriptionPromo;

  margin: 3.5rem 0 0 0;
  padding-right: 40%;
}

.button {
  margin-top: 5.5rem;
}
