@import "styles/variables";
@import "styles/mixins";

.list {
  @include resetList;

  margin-top: 2rem;
}

.listSpaced {
  .item {
    &:not(:first-child) {
      margin-top: 1rem;
    }
  }
}

.item {
  @include regular($colorDarkest);

  position: relative;
  padding-left: 3rem;

  &::before {
    --size: 0.4rem;
    content: "";
    position: absolute;
    left: 1.2rem;
    top: 0.8rem;
    width: var(--size);
    height: var(--size);
    background-color: $colorDarkest;
    border-radius: 50%;
  }

  &:not(:first-child) {
    margin-top: 0.2rem;
  }
}

.listNumbered {
  counter-reset: index;

  .item {
    @include regular($colorLightest, 500);

    padding-top: 6rem;
    padding-left: 7rem;

    & > div {
      position: relative;
    }

    &::before {
      @include counter;

      --counter: counter(index);
      counter-increment: index;
      content: counter(index);
      position: absolute;
      top: 0;
      left: 0;
      width: auto;
      height: auto;
      background-color: transparent;
      border-radius: 0;
      opacity: 0.2;
      z-index: 0;
    }

    &:nth-child(2n) {
      &::before {
        left: 1.5rem;
      }
    }
  }
}
