.wrapper {
  position: relative;
  padding-top: 10rem;
  padding-bottom: 12rem;
}

.intro {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 6.5rem;
  align-items: center;
  z-index: 1;
}

.heading {
  text-align: right;
}

.description {
  padding-right: 30%;
}

.list {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-column-gap: 3rem;
  margin-top: 10rem;
  z-index: 1;
}

.button {
  display: block;
  margin: 4.5rem auto 0 auto;
}

.circles {
  position: absolute;
  top: -5rem;
  left: -2rem;
  width: 149rem;
}
