@import "styles/variables";
@import "styles/mixins";

.heading {
  margin: 0;
}

.h1 {
  @include h1;
}

.h2 {
  @include h2;
}

.h3 {
  @include h3;
}

.h4 {
  @include h4;
}

.headingLightest {
  color: $colorLightest;
}

.headingDarkest {
  color: $colorDarkest;
}

.headingCapitalized {
  text-transform: uppercase;
}
