@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  align-items: flex-start;
  padding-top: 3.6rem;
  padding-bottom: 3.6rem;
}

.icon {
  width: 8.3rem;
  fill: $colorHighlight;
}

.button {
  @include nav;

  margin-left: auto;
}

.phone {
  @include nav($colorHighlight, 700);

  margin-left: 4rem;
}
