@import "styles/variables";

.section {
  background-color: $colorMedium;
}

.wrapper {
  padding-top: 12rem;
  padding-bottom: 12rem;
}

.heading {
  text-align: center;
}

.description {
  margin-top: 1.3rem;
  text-align: center;
  color: $colorDarkest;
}

.list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  margin-top: 1.3rem;
}

.item {
  &:not(:first-child):not(:nth-child(2)):not(:nth-child(3)) {
    margin-top: -6.5rem;
  }
}
