@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 57rem 1fr;
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 15.5rem;
  padding-top: 13rem;
  padding-bottom: 11rem;
  grid-auto-flow: column;
}

.icon {
  position: absolute;
  top: 14.4rem;
  left: calc(50% - 27rem);
  width: 37rem;
  fill: $colorLightest;
  opacity: 0.4;
}

.heading {
  grid-row: 1 / -1;
}

.slogan,
.description {
  grid-column: 2 / 3;
}

.slogan {
  @include descriptionPromo($colorLightest, 700);
}

.description {
  margin-top: 2rem;
}
