@import "variables";

// font mixins
@mixin counter ($color: $colorCounter) {
  font-family: "Rubik", Arial, sans-serif;
  font-size: 14rem;
  font-weight: 600;
  line-height: 1;
  color: $color;
}

@mixin h1 ($color: $colorHighlight) {
  font-family: "Rubik", Arial, sans-serif;
  font-size: 7rem;
  font-weight: 700;
  line-height: 8.3rem;
  color: $color;
}

@mixin h2 ($color: $colorHighlight) {
  font-family: "Rubik", Arial, sans-serif;
  font-size: 4.8rem;
  font-weight: 600;
  line-height: 1.15;
  color: $color;
}

@mixin h3 ($color: $colorHighlight, $weight: 600) {
  font-family: "Rubik", Arial, sans-serif;
  font-size: 3.6rem;
  font-weight: $weight;
  line-height: 1.15;
  color: $color;
}

@mixin h4 ($color: $colorHighlight) {
  font-family: "Rubik", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: 500;
  line-height: 2.8rem;
  color: $color;
}

@mixin descriptionPromo ($color: $colorDarkest, $weight: 400) {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 2.4rem;
  font-weight: $weight;
  line-height: 2.8rem;
  color: $color;
}

@mixin nav ($color: $colorHighlight, $weight: 500, $isHighlight: false) {
  @if ($isHighlight) {
    font-family: "Rubik", Arial, sans-serif;
  } @else {
    font-family: "Roboto", Arial, sans-serif;
  }
  font-size: 1.8rem;
  font-weight: $weight;
  line-height: 2.1rem;
  color: $color;
}

@mixin regular($color: $colorLightest, $weight: 400, $family: "Roboto") {
  font-family: $family, Arial, sans-serif;
  font-size: 1.6rem;
  font-weight: $weight;
  line-height: 1.8rem;
  color: $color;
}

@mixin step($color: $colorHighlight, $weight: 500, $family: "Rubik") {
  font-family: $family, Arial, sans-serif;
  font-size: 1.4rem;
  font-weight: $weight;
  line-height: 1.15;
  color: $color;
}
// reset mixins

@mixin resetButton {
  padding: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  border-radius: 0;
  cursor: pointer;
}

@mixin resetList {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin resetFieldset {
  margin: 0;
  padding: 0;
  border: none;
}

// style mixins
@mixin visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  font-size: 0;
}

@mixin listItem {
  @include regular($colorDarkest);

  position: relative;
  padding-left: 3rem;

  &::before {
    --size: 0.4rem;
    content: "";
    position: absolute;
    left: 1.2rem;
    top: 0.8rem;
    width: var(--size);
    height: var(--size);
    background-color: $colorDarkest;
    border-radius: 50%;
  }

  &:not(:first-child) {
    margin-top: 0.2rem;
  }
}
