@import "styles/variables";
@import "styles/mixins";

.wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 56rem;
  grid-column-gap: 20rem;
  padding-top: 12rem;
}

.description {
  @include descriptionPromo;

  margin: 2rem 0 0 0;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4rem;
  background-color: $colorHighlight;
  border-radius: 3rem;
}

.label {
  @include descriptionPromo($colorLightest);
}

.phone {
  @include h3($colorLightest);
}

.form  {
  margin-top: 2rem;
}

.circles {
  position: absolute;
  top: 16rem;
  left: -4rem;
  width: 150rem;
}

.mascot {
  position: absolute;
  bottom: -9rem;
  left: 22rem;
}
