@import "styles/variables";
@import "styles/mixins";

.footer {
  position: relative;
  background-color: $colorDarkest;
}

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 6rem;
  padding-bottom: 6rem;
}

.item {
  &:not(:first-child) {
    margin-top: 0.5rem;
  }
}

.button {
  @include resetButton;
  @include step($colorMediumDark, 400, "Roboto");

  text-decoration: underline;
}

.copy {
  @include step($colorMediumDark, 400, "Roboto");
}

.logo {
  width: 8rem;
  fill: $colorMediumDark;
}
