@import "styles/variables";
@import "styles/mixins";

.input {
  @include h3($colorDarkest, 500);

  padding: 2rem;
  width: 100%;
  line-height: 1;
  text-align: center;
  background-color: transparent;
  border: none;
  border-bottom: 3px solid $colorHighlight;

  &::placeholder {
    color: rgba(0, 0, 0, 0.5);
  }
}
