@import "styles/variables";
@import "styles/mixins";

.wrapper {
  padding-top: 12rem;
  padding-bottom: 12rem;
}

.heading {
  @include visuallyHidden;
}

.lead {
  @include h2($colorHighlight);

  text-transform: uppercase;
}

.description {
  margin-top: 2rem;
  color: $colorDarkest;
}

.list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-column-gap: 7rem;
  margin-top: 6rem;
}
