@import "styles/mixins";
@import "styles/variables";

.wrapper {
  position: relative;
  margin: 0 auto;
}

.label {
  @include step;

  position: absolute;
  top: 1.8rem;
  left: calc(50% - 18rem / 2);
  padding-top: 0.7rem;
  padding-bottom: 0.6rem;
  width: 18rem;
  text-align: center;
  letter-spacing: 4px;
  text-transform: lowercase;
  background-color: $colorLightest;
}

.labelCentered {
  top: 2.8rem;
}
