@import "../../../styles/variables";
@import "../../../styles/mixins";

.wrapper {
  position: relative;
  padding-top: 12rem;
  padding-bottom: 11rem;

  &::before {
    content: "";
    position: absolute;
    top: 26.7rem;
    left: 13rem;
    width: 59.5rem;
    height: 43.2rem;
    background-image: url("/assets/images/slider_template.png");
    background-size: cover;
    z-index: 10;
  }
}

.heading,
.description {
  grid-column: 1 / -1;
}

.description {
  @include nav($colorLightest, 400);

  margin: 0.5rem 0 0 0;
}

.sliders {
  display: grid;
  grid-template-columns: 22rem 7rem 79rem;
  margin-top: 2.2rem;

  .video {
    position: relative;
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    z-index: 3;
  }

  .info {
    grid-column: 3 / 5;
    grid-row: 1 / 2;
  }
}

.button {
  grid-column: 1 / -1;
  margin-top: 4rem;
  margin-left: auto;
}

.circles {
  position: absolute;
  top: -4rem;
  left: -2rem;
  width: 151.3rem;
}
