@import "styles/variables";
@import "styles/mixins";
@import "styles/functions";

.button {
  @include resetButton;

  transition-duration: $transitionDuration;
}

.buttonStandard {
  height: 5rem;
}

.buttonSmall {
  height: 4rem;
}

.buttonShaped {
  @include nav($colorHighlight, 400);

  padding-right: 3rem;
  padding-left: 3rem;
  border-radius: 3rem;
  box-shadow: 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
}

.buttonDark {
  color: $colorLightest;
  background-color: $colorHighlight;
  box-shadow:
    0 0.4rem 0.4rem rgba(0, 0, 0, 0.25),
    0 10rem 8rem getButtonShadowColor(0.07),
    0 4.2rem 3.3rem getButtonShadowColor(0.05),
    0 2.2rem 1.8rem getButtonShadowColor(0.04),
    0 1.3rem 1rem getButtonShadowColor(0.035),
    0 0.66rem 0.5rem getButtonShadowColor(0.028),
    0 0.28rem 0.22rem getButtonShadowColor(0.019);
}

.buttonLight {
  color: $colorHighlight;
  background-color: $colorLightest;
}
